<template>
  <view>
    <view
      class="cu-bar search fixed"
      :style="[{top:CustomBar + 'px',backgroundColor:'var(--background-color-0)'}]"
    >
      <view
        class="search-form round"
        style="background-color:var(--background-color-1)"
      >
        <text
          class="cuIcon-search"
          style="color:var(--font-color-1)"
        ></text>
        <input
          type="text"
          placeholder="请输入药材名称/拼音缩写"
          confirm-type="search"
          style="background-color:transparent;color:var(--font-color-0)"
        />
      </view>
      <view class="action">
        <button
          class="cu-btn shadow-blur round"
          style="background-color:var(--background-color-main-0);color:#fff;"
        >搜索</button>
      </view>
    </view>
    <scroll-view
      scroll-y
      class="indexes"
      :scroll-into-view="'indexes-'+ listCurID"
      :style="[{height:'calc(100vh - '+ CustomBar + 'px - 50px)'}]"
      :scroll-with-animation="true"
      :enable-back-to-top="true"
    >
      <block
        v-for="(item,index) in list"
        :key="index"
      >
        <view
          :class="'indexItem-' + item.name"
          :id="'indexes-' + item.name"
          :data-index="item.name"
        >
          <view class="padding">{{item.name}}</view>
          <view class="cu-list menu-avatar no-padding">
            <view
              class="cu-item"
              v-for="(items,sub) in 2"
              :key="sub"
            >
              <view class="cu-avatar round lg">甘</view>
              <view class="content">
                <view class="content-left"><text>甘草</text>
                  <text>0.5g/3g</text>
                  <text>￥5.12</text>
                </view>
                <image src="data:image/png;base64," />
              </view>
            </view>
          </view>
        </view>
      </block>
    </scroll-view>
    <view
      class="index-bar"
      :style="[{height:'calc(100vh - ' + CustomBar + 'px - 50px)'}]"
    >
      <view
        class="index-bar-box"
        @touchstart="tStart"
        @touchend="tEnd"
        @touchmove.stop="tMove"
      >
        <view
          class="index-bar-item"
          v-for="(item,index) in list"
          :key="index"
          :id="index"
          @touchstart="getCur"
          @touchend="setCur"
        > {{item.name}}</view>
      </view>
    </view>
    <!--选择显示-->
    <view
      v-show="!hidden"
      class="index-toast"
    >
      {{listCur}}
    </view>
  </view>
</template>
<script>
import Arrow from '@/components/arrow';
export default {
  onReady() {
    let that = this;
    uni
      .createSelectorQuery()
      .select('.index-bar-box')
      .boundingClientRect(function (res) {
        that.boxTop = res.top;
      })
      .exec();
    uni
      .createSelectorQuery()
      .select('.indexes')
      .boundingClientRect(function (res) {
        that.barTop = res.top;
      })
      .exec();
  },
  data() {
    return {
      StatusBar: this.StatusBar,
      CustomBar: this.CustomBar,
      hidden: true,
      listCurID: '',
      list: [],
      listCur: '',
    };
  },
  onReady() {
    let list = [{}];
    for (let i = 0; i < 26; i++) {
      list[i] = {};
      list[i].name = String.fromCharCode(65 + i);
    }
    this.list = list;
    this.listCur = list[0];
    let that = this;
    // uni
    //   .createSelectorQuery()
    //   .select('.index-bar-box')
    //   .boundingClientRect(function (res) {
    //     that.boxTop = res?.top;
    //   })
    //   .exec();
    // uni
    //   .createSelectorQuery()
    //   .select('.indexes')
    //   .boundingClientRect(function (res) {
    //     that.barTop = res?.top;
    //   })
    //   .exec();
  },
  methods: {
    //获取文字信息
    getCur(e) {
      this.hidden = false;
      this.listCur = this.list[e.target.id].name;
    },
    setCur(e) {
      this.hidden = true;
      this.listCur = this.listCur;
    },
    //滑动选择Item
    tMove(e) {
      let y = e.touches[0].clientY,
        offsettop = this.boxTop,
        that = this;
      //判断选择区域,只有在选择区才会生效
      if (y > offsettop) {
        let num = parseInt((y - offsettop) / 20);
        this.listCur = that.list[num].name;
      }
    },

    //触发全部开始选择
    tStart() {
      this.hidden = false;
    },

    //触发结束选择
    tEnd() {
      this.hidden = true;
      this.listCurID = this.listCur;
    },
    indexSelect(e) {
      let that = this;
      let barHeight = this.barHeight;
      let list = this.list;
      let scrollY = Math.ceil((list.length * e.detail.y) / barHeight);
      for (let i = 0; i < list.length; i++) {
        if (scrollY < i + 1) {
          that.listCur = list[i].name;
          that.movableY = i * 20;
          return false;
        }
      }
    },
  },
};
</script>

